*:focus {
  outline: 0 !important;
}
.bs-drawer {
  position: fixed;
  height: 100%;
  min-height: 100%;
  top: 0px;
  bottom: 0;
  z-index: 1001;
  padding-bottom: 0px;
}
.bs-drawer__content {
  height: 100%;
  width: 100%;
}
.bs-drawer__nav-container {
  z-index: 1000;
  height: 100%;
  width: calc(100% - 5px);
  overflow: auto;
}
.bs-drawer__nav-container-left {
  float: left;
  margin-left: -15px;
}
.bs-drawer__nav-container-right {
  float: right;
  margin-right: -15px;
}
.bs-drawer__btn {
  position: absolute;
  top: 50%;
  width: 20px;
  height: 40px;
  border-radius: 50%;
  border: 0;
  border-radius: 0;
  border-bottom-left-radius: 40px;
  border-top-left-radius: 40px;
  background: rgba(4, 80, 0, 0.4);
}
.bs-drawer__btn .bs-drawer__svg {
  position: absolute;
  fill: #fff;
  top: 20%;
  left: 10%;
  width: 80%;
  height: 60%;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.bs-drawer__btn-left {
  border-radius: 0;
  border-bottom-right-radius: 40px;
  border-top-right-radius: 40px;
}
.bs-drawer__btn-left .bs-drawer__svg {
    -webkit-transform: rotate(180deg) translate(1px, 0);
    transform: rotate(180deg) translate(1px, 0);
    width: 80%;
    height: 60%;
}
.bs-drawer__btn-left.bs-drawer__btn-open .bs-drawer__svg {
  -webkit-transform: rotate(0deg) translate(-2px, 0);
  transform: rotate(0deg) translate(-2px, 0);
  width: 80%;
  height: 60%;
}
.bs-drawer__btn-right.bs-drawer__btn-open .bs-drawer__svg {
  -webkit-transform: rotate(180deg) translate(-2px, 0);
  transform: rotate(180deg) translate(-2px, 0);
  width: 80%;
  height: 60%;
}
.bs-drawer__btn:hover {
  background: rgba(0, 0, 0, 0.8);
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.bs-drawer__btn-container {
  z-index: 1050;
  background-color: transparent;
  width: 20px;
  height: 100%;
}
.bs-drawer__btn-container-left {
  float: right;
}
.bs-drawer__btn-container-right {
  float: left;
  width: auto;
}
.bs-drawer__animate {
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
}
.bs-drawer.bs-drawer__left {
  display: block;
  overflow: auto;
  left: 0;
  -webkit-transform: translateX(-100%) translateX(35px);
          transform: translateX(-100%) translateX(35px);
}
.bs-drawer.bs-drawer__left.bs-drawer__drawer-open {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}
.bs-drawer.bs-drawer__right {
  right: 0;
  -webkit-transform: translateX(100%) translateX(-35px);
          transform: translateX(100%) translateX(-35px);
}
.bs-drawer.bs-drawer__right.bs-drawer__drawer-open {
  right: 0;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}



